body {
    font-family: Helvetica;
    margin: 0;
}

h1,h2,h3,h4,h5,h6 {
    margin-top: 0;
    font-weight: 400;
}

.main-header {
    background-color: #B22222;
    color: #fff;
    overflow: hidden;
    padding: 0.5rem;
}

.main-header h1 {
    line-height: 1;
    margin: 0;
    font-weight: 400;
}

.main-header a {
    color: white;
    text-decoration: none;
}

.main-content {
    padding: 0.5rem;
}





.search-page {
    text-align: center;
}

.search-page__input {
    border: 1px solid black;
    padding: 0.2em;
    margin-right: 1em;
    font-size: 20px;
}
.search-page__button {
    border: 1px solid black;
    background-color: #fff;
    font-size: 20px;
}





.pokemon-info {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.pokemon-info_list{
    list-style: none;
}

.pokemon-info__avatar {
    width: 200px;
    height: 200px;
    margin-right: 1em;
    float: left;
}

.pokemon-info__text {
    text-decoration: none;
    flex: 1;
}

.pokemon-info__title {
    font-size: 25px;
    text-decoration: none;
}

.pokemon-info__title, .pokemon-info__bio {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.pokemon-info__stats {
    list-style: none;
    padding: 0;
    flex: 1;
    display: flex;
}

.pokemon-info__stats a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    text-transform: uppercase;
    font-variant: small-caps;
    text-align: right;
}

.pokemon-info__stat {
    flex: 1;
}

.pokemon-info__stat p {
    margin: 1;
    white-space: nowrap;
}

.a{
    float: left
}